#App {
  text-align: center;
  min-width:1200px;
  max-width:100vw;
  height: 100vh; 

  font-family: Montserrat,helvetica neue,Helvetica,Arial,sans-serif;
 
}

#headerContent{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  max-height: 10vh;
}
#pageContent{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* justify-content: space-between; */
  margin-top: 12vh;
}


#backgroundImg:after{
  background: url('./1.png'); 
  content: '';
  background-size: cover;
  position: absolute;
  opacity: .5;
  top:0;
  bottom:0;
  left: 0;
  right:0;
}

#backgroundImg{
  position: fixed;
  z-index: -1;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background:  -webkit-gradient(linear, left top, left bottom, from(rgb(39, 95, 133)), to(rgb(179, 153, 119)));
  background:  -webkit-linear-gradient(top, rgb(39, 95, 133), rgb(179, 153, 119));
  background:  -o-linear-gradient(top, rgb(39, 95, 133), rgb(179, 153, 119));
  background:  linear-gradient(to bottom, rgb(39, 95, 133), rgb(179, 153, 119));
}
