#bioContainer{
    margin-top: 10vh;
    color: white;
    width: 70vw;
    font-family: 'Hind Guntur', sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: center;
        align-content: center;
    margin: 0 auto;
}

#bio{
    margin-top: 7vh;
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 1em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

#bio div {
    -ms-flex: 1 1;
        flex: 1 1;
}


#homeTitle{
    font-size: 2.2em;
}

#bio div:last-child {
    -ms-flex: 1.2 1;
        flex: 1.2 1;
    margin-right: 12vw;
}

#profilePic{
    height: 180px;
    border-radius: 100%;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    border: solid 2px rgba(0,0,0,0)
}

#profilePic:hover{
    border-color:  #83adef;
}




