#headerDiv{
    -ms-flex: 3 1;
        flex: 3 1;
   
    display: -ms-flexbox;
   
    display: flex;
    color: white;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    margin-top: 3vh;
    font-weight: 700;


}

#triangle{
    -ms-flex: 1 1;
        flex: 1 1;
}

#triangleImg{
    height: 13vh;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

#triangleImg:hover{
    -webkit-transform: rotate(90deg); /* Safari */
    -ms-transform: rotate(90deg);
        transform: rotate(90deg);
    -webkit-transform-origin: 50% 64% ;
        -ms-transform-origin: 50% 64% ;
            transform-origin: 50% 64% ;
}

#name{
    -ms-flex: 2 1;
        flex: 2 1;
    font-size: 4em;
    color: #224e69;
}

#name:hover{
    color: #2f5d72;
}



